@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: #cfcfcf;
  text-decoration: none;
}

a:visited {
  color: white;
}

a:hover {
  color: #ff0973;
}

main {
  font-family: 'Poppins';
  align-items: center;
  justify-content: center;
  text-align: center;
  background: #141414;
  color: #cfcfcf;
}

.principal {
  display: block;
  width: 100%;
  /* era 52% */
  min-width: 1000px;
  margin: auto;
  background: #1b1b1b;
  /* END HEADER */
  /* GLOBAL SETTINGS OF ALL SECTIONS */
  /* PARTICULAR SETTINGS OF EACH SECTION */
}

.principal hr {
  display: none;
  border: 0;
  height: 2px;
  background-image: linear-gradient(to right, transparent, transparent, #CCC, transparent, transparent);
}

.principal header {
  position: fixed;
  width: 100%;
  height: 7%;
  z-index: 9999;
  background-image: linear-gradient(#141414, transparent);
}

.principal header .nbar {
  position: absolute;
  padding-left: 1em;
  padding-right: 1em;
  display: flex;
  align-items: center;
  width: 70%;
  left: 50%;
  transform: translateX(-50%);
}

.principal header .nbar .logo-container {
  margin-right: 1em;
  align-items: center;
  text-align: left;
}

.principal header .nbar .logo-container img {
  max-width: 50px;
  min-width: 50px;
  height: auto;
  padding-top: 0.4em;
}

.principal header .nbar .left {
  flex: 4 1;
  align-items: center;
}

.principal header .nbar .left .left-nav {
  display: flex;
  list-style-type: none;
}

.principal header .nbar .left .left-nav .nav-link {
  padding-right: 1em;
}

.principal header .nbar .right {
  flex: 4 1;
}

.principal header .nbar .right .right-nav {
  display: flex;
  list-style-type: none;
  justify-content: end;
  align-items: center;
}

.principal header .nbar .right .right-nav .nav-link {
  padding-right: 0.5em;
  padding-left: 0;
  /* 
          padding-top: 1em; */
}

.principal header .nbar .right .right-nav .connect {
  /* 
          padding-top: 1em; */
}

.principal header .nbar .right .right-nav img {
  padding-top: 0.5em;
  max-width: 50px;
  min-width: 25px;
}

.principal .heroSplash {
  position: relative;
  margin-top: -5em;
  z-index: 0;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.principal .heroSplash .logoDesktop {
  width: 100%;
}

.principal .pageContent {
  position: absolute;
  background: #141414;
  top: var(--animHeight);
  z-index: 999;
  width: 70%;
  height: -webkit-max-content;
  height: max-content;
  left: 50%;
  transform: translateX(-50%);
}

.principal .mint, .principal .roadmap, .principal .team, .principal .faq {
  position: relative;
  width: 100%;
  padding: 1em;
  margin-top: 2em;
}

.principal .mint .sectionName, .principal .roadmap .sectionName, .principal .team .sectionName, .principal .faq .sectionName {
  font-size: 3em;
  margin-bottom: 0em;
  text-align: center;
}

.principal .mint .sectionName img, .principal .roadmap .sectionName img, .principal .team .sectionName img, .principal .faq .sectionName img {
  width: 30%;
  padding: 0;
  margin: 0;
}

.principal .mint {
  display: block;
}

.principal .mint .sectionContent {
  display: flex;
}

.principal .mint .left-col {
  flex: 1 1;
  padding-left: 10%;
}

.principal .mint .left-col img {
  width: 100%;
  height: auto;
  border-radius: 0;
}

.principal .mint .right-col {
  flex: 1 1;
  padding-right: 10%;
  text-align: left;
  padding-left: 3em;
}

.principal .mint .right-col img {
  width: 100%;
}

.principal .mint .right-col .mintButton {
  margin: 1em;
  text-align: center;
}

.principal .roadmap {
  display: block;
  text-align: left;
}

.principal .roadmap .roadItem {
  display: flex;
  padding-top: 2em;
  padding-bottom: 3em;
  padding-right: 1em;
  margin-bottom: 1em;
  background: #141414;
  border-radius: 0%;
}

.principal .roadmap .roadItem .roadLeftCol {
  text-align: right;
}

.principal .roadmap .roadItem .roadLeftCol .roadDate {
  font-size: 1em;
}

.principal .roadmap .roadItem .roadLeftCol .roadDesc {
  font-size: 1.3em;
}

.principal .roadmap .roadItem .roadRightCol {
  width: 100%;
  padding-left: 4em;
  text-align: left;
}

.principal .team {
  padding: 0.5em;
}

.principal .team .sectionContent {
  margin-top: 3em;
  display: flex;
}

.principal .team .sectionContent .teamMember {
  background: #141414;
  border-radius: 0%;
  padding: 1em;
  margin: 0.5em;
}

.principal .team .sectionContent .teamMember .memberPic img {
  width: 50%;
  height: auto;
  border: 5px solid #141414;
  border-radius: 50%;
  margin-top: -60%;
}

.principal .team .sectionContent .teamMember .memberName {
  align-items: center;
  display: block;
  font-weight: bold;
}

.principal .team .sectionContent .teamMember .memberName img {
  width: 2em;
  height: auto;
}

.principal .team .sectionContent .teamMember .memberTW {
  text-align: right;
}

.principal .team .sectionContent .teamMember .memberTW img {
  max-width: 50px;
  height: auto;
}

.principal .team .sectionContent .teamMember .memberDesc {
  font-style: italic;
  padding-top: 2em;
}

.principal .faq .acc {
  background: #141414;
  padding-top: 1em;
  padding-bottom: 1em;
}

.principal .footer {
  padding: 2em;
  background: #141414;
}

